code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
li Link{
  color: #6b747b !important;
}
.navbar { 
  position: sticky;
  border-radius: 30px;
  top: 20px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 98% !important;
  margin: 0 auto !important;
  height: 64px;
  background: rgba(255,255,255,0.4) !important;
  border-bottom: 1px solid rgba(225,225,225,0.5) !important;
  z-index: 1000;
}
#navBlur {
  background-color: red;
  position: fixed;
  height: 64px;
  width: 100%;
  padding-right: 16px;
  margin-left: -8px;
  margin-top: -8px;
  transition: left 1.2s ease;
  background: inherit;
  /* filter */
  
  filter: blur(10px);
  -webkit-filter: blur(10px);
  -moz-filter: blur(15px);
  -o-filter: blur(15px);
  -ms-filter: blur(15px);
  z-index: 999;
}
.nav ul {
  list-style: none;
  text-align: center;
  white-space: nowrap;
  padding-right: 32px;
}

.nav ul li {
  display: inline-block;
  color: white;
  padding: 0px 25px;
  font-size: 20px;
  text-shadow: 0 0 3px white;
}